import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index.vue'

import personal from '@/components/personal.vue'
import personalDetail from '@/components/personal-detail.vue'
import myPoint from '@/components/my-point.vue'
import pointShop from '@/components/point-shop.vue'
import pointDetails from '@/components/point-details.vue'
import myCard from '@/components/my-card.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
     {
      path: '/personal',
      name: 'personal',
      component: personal
    },
    {
      path: '/personalDetail',
      name: 'personalDetail',
      component: personalDetail
    },
    {
      path: '/myPoint',
      name: 'myPoint',
      component: myPoint
    },
    {
      path: '/pointShop',
      name: 'pointShop',
      component: pointShop
    },
    {
      path: '/pointDetails',
      name: 'pointDetails',
      component: pointDetails
    },
    {
      path: '/myCard',
      name: 'myCard',
      component: myCard
    }
    
  ]
})



